import React, { useState, useEffect } from 'react';

import chickenData from './data/Chicken.json'; // 
import pigData from './data/Pig.json'; // 
import MilkCowData from './data/MilkCow.json'; // 
import Elephant from './data/Elephant.json'; // 
import Duck from './data/Duck.json'; // 
import Bee from './data/Bee.json'; // 
import Goat from './data/Goat.json'; // 
import Sheep from './data/Sheep.json'; // 
import MeatCowData from './data/MeatCow.json'; // 
import LayerChicken from './data/LayerChicken.json'; // 
import Chicken_2 from './data/Chicken_2.json';   // 
import EggHatchingSite from './data/EggHatchingSite.json'; // 

const SearchList = ({ onSelectFeature }) => {
    const [searchTerm, setSearchTerm] = useState('');
    const [filteredData, setFilteredData] = useState([]);

    useEffect(() => {
        if (searchTerm.trim() === '') {
            setFilteredData([]); //  
            return;
        }

        //  Chicken, Pig  MilkCow
        const allFeatures = [
            ...chickenData.features.map(f => ({ ...f, type: '' })),
            ...pigData.features.map(f => ({ ...f, type: '' })),
            ...MilkCowData.features.map(f => ({ ...f, type: '' })),
            ...Elephant.features.map(f => ({ ...f, type: '' })),
            ...Duck.features.map(f => ({ ...f, type: '' })),
            ...Bee.features.map(f => ({ ...f, type: '' })),
            ...Goat.features.map(f => ({ ...f, type: '' })),
            ...Sheep.features.map(f => ({ ...f, type: '' })),
            ...MeatCowData.features.map(f => ({ ...f, type: '' })),
            ...LayerChicken.features.map(f => ({ ...f, type: '' })),
            ...Chicken_2.features.map(f => ({ ...f, type: '' })),
            ...EggHatchingSite.features.map(f => ({ ...f, type: '' })),
        ];

        //  ""
        const results = allFeatures.filter((feature) =>
            feature.properties["Farm_name"]?.toString().includes(searchTerm)
        );
        setFilteredData(results);
    }, [searchTerm]);

    const handleSearchChange = (event) => {
        setSearchTerm(event.target.value);
    };

    const handleSelect = (feature) => {
        onSelectFeature(feature); //  MapContents
        setSearchTerm(''); // 
        setFilteredData([]); // 
    };
    return (
        <div style={{
            width: '100%',
            maxWidth: '400px',
            position: 'relative',
            fontSize: '12px',
            fontWeight: 'normal',
            background: 'white',
            boxSizing: 'border-box',
            fontFamily: "'Sarabun', sans-serif"
        }}>
            <input
                type="text"
                placeholder="..."
                value={searchTerm}
                onChange={handleSearchChange}
                style={{
                    width: '100%',
                    padding: '8px',
                    fontWeight: 'normal',
                    marginBottom: '0px',
                    borderRadius: '5px',
                    border: '1px solid #ccc',
                    fontFamily: "'Sarabun', sans-serif"
                }}
            />
            {filteredData.length > 0 && (
                <ul style={{
                    position: 'absolute',
                    top: '25px',
                    left: 0,
                    width: '100%',
                    maxWidth: '400px',
                    zIndex: 2000,
                    listStyle: 'none',
                    padding: '5px',
                    maxHeight: '325px',
                    overflowY: 'auto',
                    border: '1px solid #ccc',
                    borderRadius: '5px',
                    background: '#fff',
                    boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
                }}>
                    {filteredData.map((feature, index) => (
                        <li key={index}
                            onClick={() => handleSelect(feature)}
                            style={{
                                padding: '8px',
                                cursor: 'pointer',
                                borderBottom: '1px solid #ddd',
                                textAlign: 'left'
                            }}>
                            {feature.properties["Farm_name"]}
                            <span style={{ marginLeft: '8px' }}>{feature.type === '' ? '' : feature.type === '' ? '' : feature.type === '' ? '' : feature.type === '' ? '' : feature.type === '' ? '' : feature.type === '' ? '' : feature.type === '' ? '' : feature.type === '' ? '' : feature.type === '' ? '' : feature.type === '' ? '' : feature.type === '' ? '' : feature.type === '' ? '' : ''}</span>
                        </li>
                    ))}
                </ul>
            )}
        </div>
    );
}
export default SearchList;