import React, { useEffect } from 'react';
import { useMap } from 'react-leaflet';
import L from 'leaflet';

const MapZoomToFeature = ({ feature }) => {
    const map = useMap();

    useEffect(() => {
        if (feature && feature.geometry) {
            try {
                let duration = 1.0;
                let targetZoom = 15;
                if (feature.geometry.type === 'Point') {
                    const [lon, lat] = feature.geometry.coordinates;
                    const currentZoom = map.getZoom();
                    duration = Math.min(3, Math.max(0.8, Math.abs(targetZoom - currentZoom) * 0.3));
                    map.setView([lat, lon], targetZoom, { animate: true, duration });
                } else {
                    const geoJsonData = {
                        type: 'FeatureCollection',
                        features: [feature]
                    };
                    const bounds = L.geoJSON(geoJsonData).getBounds();
                    if (bounds.isValid()) {
                        const currentZoom = map.getZoom();
                        const target = map.getBoundsZoom(bounds);
                        duration = Math.min(3, Math.max(0.8, Math.abs(target - currentZoom) * 0.3));
                        map.fitBounds(bounds, { padding: [20, 20], animate: true, duration });
                    }
                }
            } catch (error) {
                console.error('Error processing GeoJSON:', error);
            }
        }
    }, [feature, map]);

    return null;
};

export default MapZoomToFeature;