import React from 'react';
import { GeoJSON } from 'react-leaflet';
import L from 'leaflet';
import PigData from '../data/Pig.json';

const Pig = ({ onSelectFeature }) => {
    const onEachFeature = (feature, layer) => {
        const ownerName = feature.properties.Farm_name || "";

        //  Tooltip  Hover
        layer.on('mouseover', function () {
            layer.bindTooltip(ownerName, { direction: 'top', permanent: false }).openTooltip();
            if (layer.setRadius) {
                layer.setRadius(8);
            }
        });

        layer.on('mouseout', function () {
            layer.closeTooltip();
            if (layer.setRadius) {
                layer.setRadius(5);
            }
        });

        // 
        layer.on('click', () => onSelectFeature(feature));
    };

    const pointToLayer = (feature, latlng) => {
        const pigIcon = L.divIcon({
            html: `<div style="font-size: 24px; text-align: center;"></div>`,
            iconSize: [30, 30],
            className: 'pig-icon'
        });

        return L.marker(latlng, { icon: pigIcon });
    };

    return (
        <GeoJSON
            data={PigData}
            pointToLayer={pointToLayer}
            onEachFeature={onEachFeature}
        />
    );
};

export default Pig;