import React, { useState, useEffect } from 'react';
import { MapContainer, LayersControl, LayerGroup, TileLayer, Marker, Tooltip } from 'react-leaflet';
import L from 'leaflet';
import proj4 from 'proj4';

import BaseMap from './layer/BaseMap';
import Province from './layer/Province';
import Chicken from './layer/Chicken';
import Pig from './layer/Pig';
import MilkCow from './layer/MilkCow';
import Elephant from './layer/Elephant';
import Duck from './layer/Duck';
import Bee from './layer/Bee';
import Goat from './layer/Goat';
import Sheep from './layer/Sheep';
import MeatCow from './layer/MeatCow';
import LayerChicken from './layer/LayerChicken';
import Chicken_2 from './layer/Chicken_2';
import EggHatchingSite from './layer/EggHatchingSite';

import MapLegend from './MapLegend';
import MapPopup from './MapPopup';
import MapZoomToFeature from './MapZoomToFeature';
import 'leaflet.pattern';
import './map.css';

import customIconUrl from './images/Marker.png';


const CustomIcon = L.icon({
    iconUrl: customIconUrl,
    iconSize: [50, 50],
    iconAnchor: [25, 50]
});

const CustomIconLarge = L.icon({
    iconUrl: customIconUrl,
    iconSize: [70, 70], //  hover
    iconAnchor: [35, 70]
});

const MapContents = ({ isSidebarVisible, setIsSidebarVisible, selectedFeature, onSelectFeature }) => {
    const [markerPosition, setMarkerPosition] = useState(null);
    const [isMarkerHovered, setIsMarkerHovered] = useState(false);
    // Layer visibility
    const [showChicken, setShowChicken] = useState(true);
    const [showPig, setShowPig] = useState(true);
    const [showMilkCow, setShowMilkCow] = useState(true);
    const [showElephant, setShowElephant] = useState(true);
    const [showDuck, setShowDuck] = useState(true);
    const [showBee, setShowBee] = useState(true);
    const [showGoat, setShowGoat] = useState(true);
    const [showSheep, setShowSheep] = useState(true);
    const [showMeatCow, setShowMeatCow] = useState(true);
    const [showLayerChicken, setShowLayerChicken] = useState(true);
    const [showChicken_2, setShowChicken_2] = useState(true);
    const [showEggHatchingSite, setShowEggHatchingSite] = useState(true);
    // Accordion state
    const [openCategory, setOpenCategory] = useState('chicken');

    useEffect(() => {
        document.title = "LiveStock Map";
    }, []);

    const handleMapClick = (e) => {
        if (!selectedFeature) {
            setMarkerPosition(null);
        }
    };

    const handleClosePopup = () => {
        onSelectFeature(null);
        setMarkerPosition(null);
    };

    useEffect(() => {
        if (selectedFeature) {
            const centerX = selectedFeature.properties[' X (.)'];
            const centerY = selectedFeature.properties[' Y (.)'];
            if (centerX !== undefined && centerY !== undefined) {
                const [longitude, latitude] = proj4('EPSG:32647', 'EPSG:4326', [centerX, centerY]);
                setMarkerPosition([latitude, longitude]);
            } else {
                setMarkerPosition(null);
            }
        } else {
            setMarkerPosition(null);
        }
    }, [selectedFeature]);

    return (
        <>
            {/* Main Content: Sidebar + Map */}
            <div style={{ position: 'relative', width: '100%', height: '100%', minHeight: 0, overflow: 'hidden' }}>
                {/* Sidebar */}
                <div
                    className={`sidebar${isSidebarVisible ? ' open' : ''}`}
                    style={{
                        height: '80%',
                        backgroundColor: '#ffffff',
                        borderRight: isSidebarVisible ? '1px solid #ddd' : 'none',
                        overflow: isSidebarVisible ? 'auto' : 'hidden',
                        display: 'flex',
                        flexDirection: 'column',
                        position: 'absolute',
                        top: 0,
                        left: 0,
                        zIndex: 10,
                        fontFamily: "'Sarabun', sans-serif",
                        minWidth: 320,
                        maxWidth: 400,
                        width: 320,
                        boxShadow: isSidebarVisible ? 'none' : 'none',
                        transform: isSidebarVisible ? 'translateX(0)' : 'translateX(-100%)',
                        opacity: isSidebarVisible ? 1 : 0,
                        pointerEvents: isSidebarVisible ? 'auto' : 'none',
                        transition: 'transform 0.7s cubic-bezier(.4,2,.6,1), opacity 0.5s cubic-bezier(.4,2,.6,1)'
                    }}
                >
                {/* Accordion Category:  */}
                                {/* Accordion Category:  */}
                                <div style={{padding: '0 0 0 0', borderBottom: '1px solid #ddd'}}>
                                    <div
                                        style={{
                                            fontWeight: 'bold',
                                            padding: '15px',
                                            background: openCategory === 'chicken' ? '#e3f2fd' : 'transparent',
                                            cursor: 'pointer',
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'space-between',
                                            fontSize: 18
                                        }}
                                        onClick={() => setOpenCategory(openCategory === 'chicken' ? null : 'chicken')}
                                    >
                                        <span> </span>
                                        <span>{openCategory === 'chicken' ? '' : ''}</span>
                                    </div>
                                    {openCategory === 'chicken' && (
                                        <div style={{padding: '0 15px 10px 30px', display: 'flex', flexDirection: 'column', gap: 6}}>
                                            <button onClick={() => setShowChicken(!showChicken)} style={{backgroundColor: showChicken ? '#0066cc' : '#ccc', color: 'white', border: 'none', padding: '8px', borderRadius: '4px', cursor: 'pointer', fontSize: '14px', fontFamily: "'Sarabun', sans-serif"}}> </button>
                                            <button onClick={() => setShowLayerChicken(!showLayerChicken)} style={{backgroundColor: showLayerChicken ? '#0066cc' : '#ccc', color: 'white', border: 'none', padding: '8px', borderRadius: '4px', cursor: 'pointer', fontSize: '14px', fontFamily: "'Sarabun', sans-serif"}}>
                                                <span style={{width:'20px',height:'20px',verticalAlign:'middle',marginRight:'6px',display:'inline-block'}}>
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 36 36">
                                                        <path fill="#F7DECE" d="M23.142 3.403C21.492 1.899 19.712 1 18 1C12.375 1 6 10.611 6 20c0 .975.079 1.899.202 2.791C7.261 30.484 12.623 35 18 35c6 0 12-5.611 12-15c0-6.531-3.086-13.161-6.858-16.597z"/>
                                                        <path fill="#E0AA94" d="M23.142 3.403c2.908 3.519 5.108 9.018 5.108 14.453c0 9.009-5.672 14.393-11.344 14.393c-4.541 0-9.075-3.459-10.705-9.459C7.261 30.484 12.623 35 18 35c6 0 12-5.611 12-15c0-6.531-3.086-13.161-6.858-16.597z"/>
                                                    </svg>
                                                </span>
                                                
                                            </button>
                                            <button onClick={() => setShowChicken_2(!showChicken_2)} style={{backgroundColor: showChicken_2 ? '#0066cc' : '#ccc', color: 'white', border: 'none', padding: '8px', borderRadius: '4px', cursor: 'pointer', fontSize: '14px', fontFamily: "'Sarabun', sans-serif"}}> </button>
                                            <button onClick={() => setShowEggHatchingSite(!showEggHatchingSite)} style={{backgroundColor: showEggHatchingSite ? '#0066cc' : '#ccc', color: 'white', border: 'none', padding: '8px', borderRadius: '4px', cursor: 'pointer', fontSize: '14px', fontFamily: "'Sarabun', sans-serif"}}> </button>
                                        </div>
                                    )}
                                </div>
                                {/* Accordion Category:  */}
                                <div style={{padding: '0 0 0 0', borderBottom: '1px solid #ddd'}}>
                                    <div
                                        style={{
                                            fontWeight: 'bold',
                                            padding: '15px',
                                            background: openCategory === 'cow' ? '#e3f2fd' : 'transparent',
                                            cursor: 'pointer',
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'space-between',
                                            fontSize: 18
                                        }}
                                        onClick={() => setOpenCategory(openCategory === 'cow' ? null : 'cow')}
                                    >
                                        <span> </span>
                                        <span>{openCategory === 'cow' ? '' : ''}</span>
                                    </div>
                                    {openCategory === 'cow' && (
                                        <div style={{padding: '0 15px 10px 30px', display: 'flex', flexDirection: 'column', gap: 6}}>
                                            <button onClick={() => setShowMilkCow(!showMilkCow)} style={{backgroundColor: showMilkCow ? '#0066cc' : '#ccc', color: 'white', border: 'none', padding: '8px', borderRadius: '4px', cursor: 'pointer', fontSize: '14px', fontFamily: "'Sarabun', sans-serif"}}> </button>
                                            <button onClick={() => setShowMeatCow(!showMeatCow)} style={{backgroundColor: showMeatCow ? '#0066cc' : '#ccc', color: 'white', border: 'none', padding: '8px', borderRadius: '4px', cursor: 'pointer', fontSize: '14px', fontFamily: "'Sarabun', sans-serif"}}> </button>
                                        </div>
                                    )}
                                </div>
                                {/* Accordion Category:  */}
                                <div style={{padding: '0 0 0 0', borderBottom: '1px solid #ddd'}}>
                                    <div
                                        style={{
                                            fontWeight: 'bold',
                                            padding: '15px',
                                            background: openCategory === 'pig' ? '#e3f2fd' : 'transparent',
                                            cursor: 'pointer',
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'space-between',
                                            fontSize: 18
                                        }}
                                        onClick={() => setOpenCategory(openCategory === 'pig' ? null : 'pig')}
                                    >
                                        <span> </span>
                                        <span>{openCategory === 'pig' ? '' : ''}</span>
                                    </div>
                                    {openCategory === 'pig' && (
                                        <div style={{padding: '0 15px 10px 30px', display: 'flex', flexDirection: 'column', gap: 6}}>
                                            <button onClick={() => setShowPig(!showPig)} style={{backgroundColor: showPig ? '#0066cc' : '#ccc', color: 'white', border: 'none', padding: '8px', borderRadius: '4px', cursor: 'pointer', fontSize: '14px', fontFamily: "'Sarabun', sans-serif"}}> </button>
                                        </div>
                                    )}
                                </div>
                                {/* Accordion Category:  */}
                                <div style={{padding: '0 0 0 0', borderBottom: '1px solid #ddd'}}>
                                    <div
                                        style={{
                                            fontWeight: 'bold',
                                            padding: '15px',
                                            background: openCategory === 'other' ? '#e3f2fd' : 'transparent',
                                            cursor: 'pointer',
                                            display: 'flex',
                                            alignItems: 'center',
                                            justifyContent: 'space-between',
                                            fontSize: 18
                                        }}
                                        onClick={() => setOpenCategory(openCategory === 'other' ? null : 'other')}
                                    >
                                        <span></span>
                                        <span>{openCategory === 'other' ? '' : ''}</span>
                                    </div>
                                    {openCategory === 'other' && (
                                        <div style={{padding: '0 15px 10px 30px', display: 'flex', flexDirection: 'column', gap: 6}}>
                                            <button onClick={() => setShowElephant(!showElephant)} style={{backgroundColor: showElephant ? '#0066cc' : '#ccc', color: 'white', border: 'none', padding: '8px', borderRadius: '4px', cursor: 'pointer', fontSize: '14px', fontFamily: "'Sarabun', sans-serif"}}> </button>
                                            <button onClick={() => setShowDuck(!showDuck)} style={{backgroundColor: showDuck ? '#0066cc' : '#ccc', color: 'white', border: 'none', padding: '8px', borderRadius: '4px', cursor: 'pointer', fontSize: '14px', fontFamily: "'Sarabun', sans-serif"}}> </button>
                                            <button onClick={() => setShowBee(!showBee)} style={{backgroundColor: showBee ? '#0066cc' : '#ccc', color: 'white', border: 'none', padding: '8px', borderRadius: '4px', cursor: 'pointer', fontSize: '14px', fontFamily: "'Sarabun', sans-serif"}}> </button>
                                            <button onClick={() => setShowGoat(!showGoat)} style={{backgroundColor: showGoat ? '#0066cc' : '#ccc', color: 'white', border: 'none', padding: '8px', borderRadius: '4px', cursor: 'pointer', fontSize: '14px', fontFamily: "'Sarabun', sans-serif"}}> </button>
                                            <button onClick={() => setShowSheep(!showSheep)} style={{backgroundColor: showSheep ? '#0066cc' : '#ccc', color: 'white', border: 'none', padding: '8px', borderRadius: '4px', cursor: 'pointer', fontSize: '14px', fontFamily: "'Sarabun', sans-serif"}}> </button>
                                        </div>
                                    )}
                                </div>
                                {/* Search Box */}
                                {/* Search Box  Topbar  */}
                                {/* Close Button */}
                    <button onClick={() => setIsSidebarVisible(false)} style={{ display: 'none' }}>{'<'}</button>
            </div>
            {/* MapContainer */}
                {/* MapContainer */}
                <div className="map-container" style={{
                    height: '100vh',
                    marginLeft: isSidebarVisible ? 320 : 0,
                    transition: 'margin-left 0.7s cubic-bezier(.4,2,.6,1)'
                }}>
                    <MapContainer
                        style={{ width: '100%', height: '100%' }}
                        center={[13, 100]}
                        zoom={6}
                        scrollWheelZoom={true}
                        onClick={handleMapClick}
                        zoomAnimation={true}
                        zoomAnimationDuration={2.0}
                        animate={true}
                    >
                        <TileLayer
                            attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                        />
                        <LayersControl position="topright">
                            <BaseMap />

                            <LayersControl.Overlay name="" checked>
                                <LayerGroup>
                                    <Province />
                                </LayerGroup>
                            </LayersControl.Overlay>

                        </LayersControl>

                        {showChicken && (
                            <LayerGroup>
                                <Chicken onSelectFeature={onSelectFeature} />
                            </LayerGroup>
                        )}

                        {showPig && (
                            <LayerGroup>
                                <Pig onSelectFeature={onSelectFeature} />
                            </LayerGroup>
                        )}

                        {showMilkCow && (
                            <LayerGroup>
                                <MilkCow onSelectFeature={onSelectFeature} />
                            </LayerGroup>
                        )}

                        {showElephant && (
                            <LayerGroup>
                                <Elephant onSelectFeature={onSelectFeature} />
                            </LayerGroup>
                        )}

                        {showDuck && (
                            <LayerGroup>
                                <Duck onSelectFeature={onSelectFeature} />
                            </LayerGroup>
                        )}

                        {showBee && (
                            <LayerGroup>
                                <Bee onSelectFeature={onSelectFeature} />
                            </LayerGroup>
                        )}

                        {showGoat && (
                            <LayerGroup>
                                <Goat onSelectFeature={onSelectFeature} />
                            </LayerGroup>
                        )}

                        {showSheep && (
                            <LayerGroup>
                                <Sheep onSelectFeature={onSelectFeature} />
                            </LayerGroup>
                        )}

                        {showMeatCow && (
                            <LayerGroup>
                                <MeatCow onSelectFeature={onSelectFeature} />
                            </LayerGroup>
                        )}

                        {showLayerChicken && (
                            <LayerGroup>
                                <LayerChicken onSelectFeature={onSelectFeature} />
                            </LayerGroup>
                        )}

                        {showChicken_2 && (
                            <LayerGroup>
                                <Chicken_2 onSelectFeature={onSelectFeature} />
                            </LayerGroup>
                        )}

                        {showEggHatchingSite && (
                            <LayerGroup>
                                <EggHatchingSite onSelectFeature={onSelectFeature} />
                            </LayerGroup>
                        )}

                        <MapZoomToFeature feature={selectedFeature} />
                        <MapLegend />
                        {selectedFeature && (
                            <MapPopup feature={selectedFeature} onClose={handleClosePopup} />
                        )}
                        {markerPosition && (
                            <Marker
                                position={markerPosition}
                                icon={isMarkerHovered ? CustomIconLarge : CustomIcon}
                                interactive={true}
                                eventHandlers={{
                                    mouseover: () => {
                                        setIsMarkerHovered(true);
                                    },
                                    mouseout: () => {
                                        setIsMarkerHovered(false);
                                    }
                                }}
                            >
                                <Tooltip opacity={0} direction="top" permanent={false} />
                            </Marker>
                        )}
                    </MapContainer>
                </div>
            </div>
        </>
    );
};

export default MapContents;