import './App.css';
import MapContents from './components/MapContents';
import SearchList from './components/SearchList';
import 'leaflet/dist/leaflet.css';
import { useState } from 'react';

function App() {
  const [isSidebarVisible, setIsSidebarVisible] = useState(true);
  const [selectedFeature, setSelectedFeature] = useState(null);

  const toggleSidebar = () => {
    setIsSidebarVisible(!isSidebarVisible);
  };

  //  SearchList  MapContents
  const handleSelectFeature = (feature) => {
    setSelectedFeature(feature);
  };

  return (
    <div style={{display:'flex', flexDirection:'column', height:'100vh', width:'100vw', overflow:'hidden'}}>
      {/* Topbar  */}
      <div className="top-bar" style={{display:'flex',flexDirection:'row',alignItems:'center',height:'60px', borderBottom:'1px solid #eee', background:'#fff', flexShrink:0}}>
        <div style={{width:'330px',display:'flex',alignItems:'center',height:'100%'}}>
          <div style={{width:'305px',display:'flex',alignItems:'center',height:'100%',paddingLeft:0,marginLeft:0}}>
            <button
              onClick={toggleSidebar}
              style={{
                marginLeft: '15px',
                backgroundColor: 'transparent',
                color: '#333',
                border: 'none',
                fontSize: '28px',
                cursor: 'pointer',
                padding: '0',
                display: 'flex',
                alignItems: 'center',
                zIndex: 1001
              }}
              aria-label={isSidebarVisible ? '' : ''}
            >
              {isSidebarVisible ? <span style={{fontWeight:'bold'}}>&#8592;</span> : <span style={{fontWeight:'bold'}}>&#9776;</span>}
            </button>
            <img src={`${process.env.PUBLIC_URL}/assets/logo.png`} alt="Logo" style={{ height: '40px', marginLeft: '16px' }} />
          </div>
          <div style={{height:'40px',width:'1px',background:'#ddd',margin:'0 0 0 0'}}></div>
          <div style={{flex:1}}></div>
        </div>
        {/* SearchList  Topbar */}
        <div style={{flex:1, display:'flex', justifyContent:'center', alignItems:'center', height:'100%'}}>
          <div style={{ width: 400, maxWidth: '90%', height:'100%', display:'flex', alignItems:'center' }}>
            <SearchList onSelectFeature={handleSelectFeature} />
          </div>
        </div>
      </div>
      {/* Main Content */}
      <div style={{flex:1, minHeight:0, overflow:'hidden'}}>
        <MapContents isSidebarVisible={isSidebarVisible} setIsSidebarVisible={setIsSidebarVisible} selectedFeature={selectedFeature} onSelectFeature={handleSelectFeature} />
      </div>
    </div>
  );
}

export default App;
