import React from 'react';
import { GeoJSON } from 'react-leaflet';
import L from 'leaflet';
import LayerChickenData from '../data/LayerChicken.json';

const Layer = ({ onSelectFeature }) => {
    const onEachFeature = (feature, layer) => {
        const ownerName = feature.properties.Farm_name || "";

        //  Tooltip  Hover
        layer.on('mouseover', function () {
            layer.bindTooltip(ownerName, { direction: 'top', permanent: false }).openTooltip();
            if (layer.setRadius) {
                layer.setRadius(8);
            }
        });

        layer.on('mouseout', function () {
            layer.closeTooltip();
            if (layer.setRadius) {
                layer.setRadius(5);
            }
        });

        // 
        layer.on('click', () => onSelectFeature(feature));
    };

    const pointToLayer = (feature, latlng) => {
        const LayerChickenIcon = L.divIcon({
            html: `<span style="display:block;margin:auto;width:30px;height:30px;">
                <svg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 36 36'>
                    <path fill='#F7DECE' d='M23.142 3.403C21.492 1.899 19.712 1 18 1C12.375 1 6 10.611 6 20c0 .975.079 1.899.202 2.791C7.261 30.484 12.623 35 18 35c6 0 12-5.611 12-15c0-6.531-3.086-13.161-6.858-16.597z'/>
                    <path fill='#E0AA94' d='M23.142 3.403c2.908 3.519 5.108 9.018 5.108 14.453c0 9.009-5.672 14.393-11.344 14.393c-4.541 0-9.075-3.459-10.705-9.459C7.261 30.484 12.623 35 18 35c6 0 12-5.611 12-15c0-6.531-3.086-13.161-6.858-16.597z'/>
                </svg>
            </span>`,
            iconSize: [30, 30],
            className: 'chicken-icon'
        });

        return L.marker(latlng, { icon: LayerChickenIcon });
    };

    return (
        <GeoJSON
            data={LayerChickenData}
            pointToLayer={pointToLayer}
            onEachFeature={onEachFeature}
        />
    );
};

export default Layer;