
import React, { useEffect, useRef } from 'react';

const MapPopup = ({ feature, onClose }) => {
    const imgRef = useRef(null);
    useEffect(() => {
        // Reset image display when feature changes
        if (imgRef.current) {
            imgRef.current.style.display = '';
        }
    }, [feature]);
    if (!feature) return null;

    const wrapperStyle = {
        position: 'fixed',
        right: 32,
        bottom: 32,
        zIndex: 1000,
        pointerEvents: 'auto',
        maxWidth: 400,
    };
    const popupStyle = {
        background: 'white',
        padding: '18px 18px 12px 18px',
        borderRadius: '16px',
        boxShadow: '0 4px 20px rgba(0,0,0,0.2)',
        minWidth: '320px',
        maxWidth: '350px',
        fontFamily: 'Prompt, Arial, sans-serif',
        position: 'relative',
    };

    const properties = feature?.properties || {};
    const title = properties[''] || properties['Farm_name'] || '';
    const value1 = properties['Address'] || '';
    const value2 = properties['Affiliatio'] || '';
    const value3 = properties['Operator_n'] || '';
    const value4 = properties['Animal_qua'] || '';
    const unit = properties[''] || '';

    return (
        <div style={wrapperStyle}>
            <div style={popupStyle}>
                <button
                    onClick={onClose}
                    style={{
                        position: 'absolute',
                        top: '10px',
                        right: '10px',
                        background: 'transparent',
                        border: 'none',
                        fontSize: '24px',
                        cursor: 'pointer',
                        color: '#888',
                    }}
                >
                    
                </button>
                <div style={{ fontWeight: 'bold', fontSize: '20px', textAlign: 'center', marginBottom: '10px' }}>
                    {title}
                </div>
                {properties['Farm_name'] && (
                    <div style={{ textAlign: 'center', marginBottom: '10px' }}>
                        <img
                            ref={imgRef}
                            src={process.env.PUBLIC_URL + '/assets/' + properties['Farm_name'] + '.png'}
                            alt={properties['Farm_name']}
                            style={{
                                maxWidth: '120px',
                                maxHeight: '80px',
                                objectFit: 'contain',
                                borderRadius: '8px',
                                boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
                            }}
                            onError={(e) => (e.target.style.display = 'none')}
                        />
                    </div>
                )}
                <table style={{ width: '100%', fontSize: '15px' }}>
                    <tbody>
                        <tr>
                            <td></td>
                            <td style={{ textAlign: 'right' }}>{value1}</td>
                        </tr>
                        <tr>
                            <td></td>
                            <td style={{ textAlign: 'right' }}>{value2}</td>
                        </tr>
                        <tr>
                            <td></td>
                            <td style={{ textAlign: 'right' }}>{value3}</td>
                        </tr>
                        <tr>
                            <td></td>
                            <td style={{ textAlign: 'right' }}>
                                {value4} {unit}
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    );
};

export default MapPopup;
